.nav-content {
  display: grid;
  grid-template: auto / 1fr 1fr 1fr 1fr 1fr 1fr;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.nav-links {
  text-decoration: none;
  color: var(--main-bg-color-1);
}

.nav-links:hover {
  color: var(--accent-color-1);
}

.shopping-cart-article-holder {
  display: flex;
  position: fixed;
  right: 10%;
  align-items: center;
  text-align: center;
}

.shopping-cart-article-holder:hover {
  transform: scale(1.1);
  color: black;
}

.shopping-cart-article-counter {
  display: block;
  position: relative;
  bottom: 10%;
  right: -15%;
  color: black;
  font-size: 1.5rem;
  width: 2rem;
  background-color: rgb(197, 131, 7);
  border-radius: 50%;
}

.cart-svg {
  display: flex;
  background-color: var(--main-bg-color-1);
  /* outline: .2rem solid var(--main-bg-color-2); */
  border-radius: 50%;
  width: fit-content;
  height: fit-content;
  padding: 1rem;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

#cart {
  position: fixed;
  left: 0%;
  top: 0%;
  height: 100%;
  width: 100%;
  background-color: rgba(128, 128, 128, 0.5);
  z-index: 9;
}

.shopping-cart-container {
  position: relative;
  display: grid;
  grid-template: auto 1fr auto / auto;
  left: 75%;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: var(--main-bg-color-1);
  height: 100%;
  width: 25%;
  border-left: 0.3rem solid var(--accent-color-1);
  overflow-y: scroll;
  overflow-x: hidden;
  z-index: 99;
  justify-self: start;
}

.toggle-cart-display {
  display: none;
}

.shopping-cart-header-container {
  display: grid;
  grid-template: auto / 1fr 1fr;
  align-content: space-between;
  justify-content: space-between;
  align-items: center;
  justify-items: center;
  width: 100%;
  background-color: var(--main-bg-color-2);
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.close-shopping-cart {
  /* position: absolute;
    top: 3.2%;
    left: 70%;
    width: 20%; */
  cursor: pointer;
  color: black;
  border: 0.2rem solid var(--accent-color-1);
  background-color: white;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  font-weight: 700;
  height: fit-content;
  justify-self: end;
  margin-right: 10%;
  border-radius: 0.5rem;
}

.shopping-cart-headline {
  width: max-content;
  height: min-content;
  font-weight: 700;
}

.shopping-cart-header-amount-items {
  font-size: 1rem;
  color: rgb(243, 243, 243);
  background-color: gray;
  border-radius: 0.5rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  font-weight: 400;
}

.shopping-cart-article-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
}

.shopping-cart-empty-cart {
  color: var(--main-bg-color-2);
  margin-top: 3rem;
  font-size: 2rem;
  font-weight: 700;
}

.shopping-cart-item {
  display: flex;
  align-items: center;
  justify-items: center;
  justify-content: space-around;
  align-content: center;
  border-bottom: 0.2rem solid var(--accent-color-1);
  color: var(--main-bg-color-2);
  /* border-radius: 1rem; */
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.shopping-cart-image-container {
  display: flex;
  justify-content: center;
  height: 25vh;
}

.shopping-cart-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: 0% 20%;
  border-radius: 0.5rem;
}

.shopping-cart-product-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  gap: 1rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.shopping-cart-product-amount-container {
  position: relative;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-items: center;
  align-content: center;
  justify-content: center;
  font-size: 3rem;
}

.shopping-cart-product-amount-control {
  position: relative;
  bottom: 0.5vh;
  cursor: pointer;
  width: 2rem;
  font-size: 2rem;
}

.shopping-cart-product-amount {
  padding: 0.25rem;
  padding-left: 1rem;
  padding-right: 1rem;
  height: 2rem;
  width: 1rem;
  border: 0.15rem solid var(--accent-color-1);
  border-radius: 0.5rem;
  background-color: var(--main-bg-color-1);
  text-align: center;
  font-size: 1.5rem;
}

.shopping-cart-product-amount:focus {
  outline: 0.15rem solid var(--accent-color-1);
  border-radius: 0.5rem;
}

.shopping-cart-dollar-sign {
  color: var(--accent-color-2);
}

.shopping-cart-remove-product {
  font-size: 1rem;
  padding: 0.5rem;
  width: fit-content;
  align-self: center;
  background-color: var(--main-bg-color-2);
  color: black;
  color: var(--main-bg-color-1);
  border-radius: 0.5rem;
  cursor: pointer;
  align-self: center;
}

.shopping-cart-lower-container {
  display: flex;
  justify-self: end;
  flex-direction: column;
  align-items: center;
  background-color: var(--main-bg-color-2);
  color: black;
  width: 100%;
}

.shopping-cart-total-price-container {
  display: flex;
  width: 100%;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-bottom: 5%;
  justify-content: space-around;
  padding-top: 2rem;
  color: var(--main-bg-color-1);
  font-weight: 600;
}

.shopping-cart-total-price-amount {
  font-size: 3rem;
  font-weight: 700;
  background-color: var(--main-bg-color-1);
  border: 0.2rem solid var(--accent-color-2);
  color: var(--main-bg-color-2);
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.shopping-cart-proceed-to-checkout {
  padding: 1rem;
  background-color: var(--accent-color-1);
  border-radius: 1rem;
  font-weight: 700;
  cursor: pointer;
  width: fit-content;
  text-align: center;
  margin-bottom: 3rem;
}
